/*
    Type check for basic classes, and some(as Array) others.
    Basically, we can check any other type with instanceof or checking its constructor value.
 */
class TypeCheck {

    static isString(val) {
        return typeof val === "string" || val instanceof String;
    }

    static isNumber(val) {
        return typeof val === "number" || val instanceof Number;
    }

    static isFiniteNumber(val) {
        return this.isNumber(val) && isFinite(val);
    }

    static isArray(val) {
        return val.constructor === Array;
    }

    static isFunction(val) {
        return val.constructor === Function;
    }

    static isObject(val) {
        return typeof(val) === "object";
    }

    static isNull(val) {
        return val === null;
    }

    static isUndefined(val) {
        return val === undefined;
    }

    static isBool(val) {
        return typeof(val) === "boolean" || val instanceof Boolean;
    }

    static isSymbol(val) {
        return typeof(val) === "symbol" || val instanceof Symbol;
    }

    static objectKeysContain(obj, key) {
        return key in obj;
    }

    static objectValuesContain(obj, val) {
        return Object.values(obj).indexOf(val) !== -1;
    }

    static isElement(val) {
        return val instanceof Element || val instanceof HTMLDocument;
    }

    static isDefined(val) {
        return val !== undefined;
    }

    static isArrayOf(val, checkFunc) {
        return TypeCheck.isAnd(
            TypeCheck.isArray(val),
            val.length !== 0,
            val.every((item) => checkFunc(item)) 
        );
    }

    static assert(cond, errorStr) {
        if(!this.isBool(cond)) throw new Error("assert() - not boolean in arguments");
        if(cond === false) throw new Error("assert() - fail: " + errorStr);
    }

    /*
        Throws if not all arguments evaluate to true.
    */
    static assertAnd(/* arguments */) {
        if(arguments.length === 0) return;
        let hasErrorStr = this.isString(arguments[arguments.length - 1]);
        let errorStr = hasErrorStr ? arguments[arguments.length - 1] : "";
        let conditionsLength = hasErrorStr ? arguments.length - 1 : arguments.length;
        for(let i = 0; i < conditionsLength; ++i) {
            if(!this.isBool(arguments[i])) throw new Error("assertAnd() - not boolean in arguments");
            if(arguments[i] === true) continue;
            else throw new Error("assertAnd() - fail at argument " + i + ": " + errorStr);
        }
    }

    /*
        returns false if not all conditions are true
    */
    static isAnd(/* arguments */) {
        for(let i = 0; i < arguments.length; ++i) {
            if(arguments[i] !== true) return false;
        }
        return true;
    }

    /*
        If some argument is true, not throws.
    */
    static assertOr(/* arguments */) {
        if(arguments.length === 0) return;
        let hasErrorStr = this.isString(arguments[arguments.length - 1]);
        let errorStr = hasErrorStr ? arguments[arguments.length - 1] : "";
        let conditionsLength = hasErrorStr ? arguments.length - 1 : arguments.length;
        for(let i = 0; i < conditionsLength; ++i) {
            if(!this.isBool(arguments[i])) throw new Error("assertOr() - not boolean in arguments");
            if(arguments[i] === true) return;
            else continue;
        }
        throw new Error("assertOr() - fail at argument " + i + ": " + errorStr);
    }

    /*
        If one of arguments is true, return true.
    */
    static isOr(/* arguments */) {
        for(let i = 0; i < arguments.length; ++i) {
            if(arguments[i] === true) return true;
        }
        return false;
    }

    /*
        Sets obj[key] = val if obj[key] is undefined
    */
    static setObjDefault(obj, key, val) {
        if (!(key in obj)) obj[key] = val;
        return obj;
    }

    /*
        Returns object like {
            result: [bool] - true if all rules are correct,
            status: [string] - "ok" or error text of which rule has failed
        }
        rules should be like:
            "key1": (item) => TC.isString(item),
            "key1": (item) => TC.isDefined(item),
            "key3": (item) => TC.assertOr(TC.isNumber(item), TC.isString(item)),
            ...
    */
    static checkObj(obj, rules) {
        let num = 0;
        for (let ruleKey in rules) {
            if(!rules[ruleKey](obj[ruleKey], obj)) return {
                result: false,
                status: `Rule number ${num} is not correct`,
            }
            ++num;
        }
        return {
            result: true,
            status: "ok",
        };
    }
}


module.exports = TypeCheck;
